"use client"
import { Button, Navbar, NavbarBrand, NavbarItem } from "@nextui-org/react";
import { redirect, useRouter } from "next/navigation";
import querystring from 'querystring';
import { getCookies } from "../helpers/get-cookies";
import { clearCookies } from "../helpers/clear-cookies";
import { useState } from "react";

export default function MainAppBar() {
    const router = useRouter();
    const { accessToken, userInfo} = getCookies();
    const [ accessTokenState, setAccessToken ] = useState<string | null>(accessToken);
    const [ userInfoState, setUserInfo ] = useState<string | null>(userInfo);

    const signIn = async () => {
        const state = window.location.href;
        redirect('https://accounts.spotify.com/authorize?' +
            querystring.stringify({
                response_type: 'code',
                client_id: process.env.NEXT_PUBLIC_SPOTIFY_CLIENT_ID,
                scope: 'playlist-read-private playlist-read-collaborative playlist-modify-public playlist-modify-private user-read-private user-read-email',
                redirect_uri: 'http://localhost:3000/api/callback',
                state: state,
                show_dialog: true
            })
        );
    }

    const signOut = async () => {
        clearCookies();
        setAccessToken(null);
        setUserInfo(null);
    }

    return (
        <Navbar 
            className="text-white bg-black"
            maxWidth="full"    
        >
            <NavbarBrand>
                <Button 
                    onPress={() => router.push("/")}
                    className="text-white"
                    variant="bordered"
                >
                    Playlist Knapsack
                </Button>
            </NavbarBrand>
            {!userInfo && (
                <NavbarItem>
                    <Button onPress={() => signIn()}>
                        Sign in
                    </Button>
                </NavbarItem>
            )}
            {userInfo && (
                <NavbarItem>
                    <Button onPress={() => signOut()}>
                        Sign out
                    </Button>
                </NavbarItem>
            )}
        </Navbar>
    )
}